import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
// render에서는 단 하나의 component, 즉 app만 실행되어야 한다.
ReactDOM.render(
  <React.StrictMode>
    <App /> 
    {/* # component : html을 반환하는 함수 --> JSX(JS와 HTML의 이러한 관계) */}
  </React.StrictMode>,
  document.getElementById('root')
);

